import React, { useEffect, useState } from "react";
import "./App.css";
import "bootstrap/dist/css/bootstrap.min.css";
import * as ReactBootstrap from "react-bootstrap";

function App() {
  const url = "https://api.adviceslip.com/advice";

  const [joke, setJoke] = useState("");
  const [loading, setLoading] = useState(true);
  const fetchJokes = async () => {
    setLoading(true);
    const data = await fetch(url);
    const response = await data.json();

    setTimeout(() => {
      setJoke(response["slip"]["advice"]);
      setLoading(false);
    }, 1500);
  };

  useEffect(() => {
    fetchJokes();
  }, []);
  return (
    <>
      <div className="App">
        <div className="card">
          <div className="box">
            <div className="bold">
              {loading ? (
                <ReactBootstrap.Spinner animation="grow" variant="danger" />
              ) : (
                joke
              )}
            </div>

            <div style={{ marginBottom: "30px" }}>
              <button
                type="button"
                className=" btn-primary-btn"
                onClick={fetchJokes}
              >
                Get Advice
              </button>
            </div>

            <span>
              <ul>
                <li>
                  <a
                    href="https://www.facebook.com/akshay.kumar.37051"
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    <i className="fa fa-facebook" aria-hidden="true"></i>
                  </a>
                </li>
                <li>
                  <a
                    href="https://github.com/TheCodeDecode"
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    <i className="fa fa-github" aria-hidden="true"></i>
                  </a>
                </li>

                <li>
                  <a
                    href="https://www.linkedin.com/in/akshay-kumar-8b0112108/"
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    <i className="fa fa-linkedin" aria-hidden="true"></i>
                  </a>
                </li>
                <li>
                  <a
                    href="https://www.instagram.com/akkshhyyy"
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    <i className="fa fa-instagram" aria-hidden="true"></i>
                  </a>
                </li>
              </ul>
            </span>
          </div>
        </div>
      </div>
    </>
  );
}

export default App;
